body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fff9f1;
  background-size: contain;
}
.card {
  padding: 15px;
}
form {
  padding: 15px;
  margin-top:30px;
}
form button, form h5 {
  margin: 20px 0;
}
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=URL]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materialize-textarea:focus:not([readonly]) {
  border-color: #ec407a;
  box-shadow: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footnote-text {
  font-size: 60%;
}
.footnote-text::before {
  content: '*';
}

.peters_logo a {
  display: flex;
  flex-direction: row;
  align-items: center;
  white-space: nowrap;
  z-index: 16;
}
.main-page-links {
  white-space: nowrap;
}
.peters_logo h1{
  color: #fff;
  font-size: 26px;
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 608px) {
  .peters_logo h1{
    display: none;
  }
}
.peters_logo img{
  max-width: 30px;
  max-height: 30px;
  margin: 15px;
}
.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #424242;
}
.navbar ul {
  display: flex;
  flex-direction: row;
}
@media screen and (max-width: 350px) {
  .peters_logo a{
    width: 100%;
  }
  .navbar {
    flex-direction: column;
    align-content: center;
    justify-content: center;
  }
  .peters_logo img {
    margin: 0 auto;
    margin-top: 15px;
  }
  .navbar ul {
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
  }
}

nav{
  height: auto;
}

.img-responsive{
  max-width: 100%;
}

.project-summary {
  padding: 15px;
  overflow: hidden;
}

.project-summary .card-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 350px;
}
@media screen and (max-width: 704px) {
  .project-summary .card-content {
    height: 400px;
  }
}
@media screen and (max-width: 600px) {
  .project-summary .card-content {
    height: auto;
  }
}
.project-summary span {
  height: auto;
  overflow: hidden;
}

.project-summary .detail_holder .grey-text{
  white-space: nowrap;
}

.project-summary .image_holder{
  overflow: hidden;
  display: flex;
  position: relative;
  height: 120px;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

.project-summary .video_holder{
  overflow: hidden;
}

.project-summary .card-content .card-title {
  margin: 5px 0;
  order: 0;
}
.project-summary .card-content .image_holder, .project-summary .card-content .video_holder {
  margin: 5px 0;
  order: 1;
}
.project-summary .card-content .content_holder{
  order: 2;
}
.project-summary .card-content .detail_holder{
  order: 99;
}

.project-summary .content_holder{
  height: 85px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  margin: 15px 0;
}
.project-summary .content_holder.noimage{
  height: auto;
}

.project-summary.card .card-content{
  padding: 0;
}

