.project-summary .image_holder :nth-child(1) {
  max-width: 100%;
  width: 100%;
  position: absolute;
}

.details-go_back_button {
  position: absolute;
  top: 5px;
  right: auto;
  left: 5px;
}
.details-delete_button {
  position: absolute;
  top: 5px;
  right: 5px;
  left: auto;
}
.details-edit_button {
  position: absolute;
  top: 5px;
  right: 45px;
  left: auto;
}

.white-overlay_box {
  display: block;
  position: absolute;
  width: 100%;
  height: 70%;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  background: transparent;
  background: linear-gradient(transparent , white);
}

.notifications li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 5px;
  border-bottom: 1px solid #efefef;
}

.nopadding-bottom {
  padding-bottom: 0 !important;
}

#vgs-control-replay {
  display: none;
}

.responsive-video video{
  width: 100%;
}